//
// Load core variables and mixins
// --------------------------------------------------
@import "variables.less";
@import "mixins.less";

@body-color: #444;

@btn-cancel-color: @brand-primary;
@btn-cancel-bg: #FFF;
@btn-cancel-border: #FFF;

@form-border-radius: 3px;

@panel-bg-footer: #FFF;

//Theme Cloud

hr {
  border-top-color: #efefe7;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

body {
  color: @body-color;
}

.jumbotron h1,
.jumbotron h2,
.jumbotron h3,
.jumbotron h4,
.jumbotron h5,
.jumbotron h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: @body-color;
}

.btn-cancel {
  .button-variant(@btn-cancel-color; @btn-cancel-bg; @btn-cancel-border);
  text-decoration: underline;
  &:hover,
  &:focus,
  &:active,
  &.active {
    text-decoration: underline;
  }
}

.btn {
  border-radius: @form-border-radius;
}

.btn-primary {
  #gradient.vertical(#E7841A; #E07D1A; 0%; 100%);
  border: 1px solid #D17219;
  text-shadow: 0 1px rgba(0,0,0,0.1);
  &:hover { #gradient.vertical(#F48C1D; #E07D1A; 0%; 100%); }

}

.form-control {
  .form-control-focus(@brand-primary);
  .placeholder(@gray-light);

  border-radius: @form-border-radius;
}

.table.table-bordered > caption + thead > tr:first-child > th,
.table.table-bordered > colgroup + thead > tr:first-child > th,
.table.table-bordered > thead:first-child > tr:first-child > th,
.table.table-bordered th {
  background-color: lighten(@brand-primary, 45%);
  color: darken(@brand-primary, 10%);
}

.table-bordered > thead > tr > th,
.table-bordered > tbody > tr > th,
.table-bordered > tfoot > tr > th,
.table-bordered > thead > tr > td {
  border-bottom: solid 1px darken(@brand-primary, 5%);
}

.panel {
  border-radius: 5px;
  position: relative;
}

.modal-content::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 5px;
  border-bottom: solid 1px lighten(#000, 92%);
}

.btn-left {
  float: left;
}

.btn-right {
  float: right;
}

/*
  JUST FOR DOCUMENTATION
*/

.page-header {
  margin-top: 0;
}

.bs-docs-sidebar .nav > .active > a,
.bs-docs-sidebar .nav > .active:hover > a,
.bs-docs-sidebar .nav > .active:focus > a {
  padding-left: 18px;
  font-weight: 700;
  color: #563d7c;
  background-color: transparent;
  border-left: 2px solid #563d7c;
}

.bs-docs-sidebar .nav > li > a {
  display: block;
  font-size: 13px;
  font-weight: 500;
  color: #999;
  padding: 4px 20px;
}

form .alert {
  padding: 2px 12px;
}

.highlight {
  margin-top: 20px;
}

.ex-modal.modal {
  position: relative;
  top: auto;
  right: auto;
  left: auto;
  bottom: auto;
  z-index: 1;
  display: block;
}

.icon-inline span {
  display: inline-block;
  vertical-align: middle;
}

.contrast-white .panel-1 {
  background-color: #444;
  color: #B3B3B3;
}

.panel-border .panel-0,
.panel-border .panel-1 {
  border: solid 1px #ddd;
}

.panel-0,
.panel-1 {
  padding: 1em;
}

.min-height {

  min-height: 300px;
}

/***************************/

.bz-cd-brand {
  background-image: url(../img/brand-logo.png);
  background-repeat: no-repeat;
  width: 191px;
  height: 84px;
}

.bz-brand-color {
  background-color: @brand-primary;
}

.bz-cd-brand-white {
  background-image: url(../img/brand-logo-white.png);
  background-repeat: no-repeat;
  width: 126px;
  height: 60px;
}

.bz-cd-brand-img {
  text-align: center;
}

.bz-cd-nolabel label {
  display: none;
}

.bz-cd-panel-default {
  position: relative;
}

.bz-cd-panel-default .panel-footer {
  background-color: @panel-bg-footer;
  padding: 6px 12px;
}

.bz-cd-panel-close {
  position: absolute;
  z-index: 10;
  top: -6px;
  right: -6px;
  border: 1px solid transparent;
  border-radius: 24px;
  background-color: #fff;
  line-height: 0;
}

.bz-cd-modal p {
  font-size: 1.2em;
}

.bz-cd-hidden {
  display: none;
  visibility: hidden;
}

.modal-dialog .bz-cd-icon-close {
  background-color: #FFF;
  position: absolute;
  top: -10px;
  right: -10px;
  opacity: 0.8;
  z-index: 200;
}

.btn,
.btn:active,
.btn.active {
  &:focus {
    outline: 5px auto @brand-primary;
  }
}

.navbar.navbar-default,
.navbar.navbar-inverse {
  border-radius: 0;
}

.navbar-inverse .navbar-toggle {
  border: none;
}

.navbar-inverse .navbar-toggle {
  &:hover, &:focus {
    background: darken(@brand-primary, 10%);
  }
}

.bz-cd-nav .divider {
  height: 64px;
  border-left: 1px solid darken(@brand-primary, 3%);
  border-right: 1px solid lighten(@brand-primary, 3%);
  margin: 0 1em;
}

.bz-cd-wrapper {
  background: #f0f0f0;
}

.divider {
  border-left: solid 1px @body-color;
  height: 1em;
  margin: 0 0.25em;
}

.bz-cd-username {
  margin-right: 1em;
}

.bz-cd-inline > * {
  display: inline-block;
  vertical-align: middle;
}

.bz-cd-panel.right {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  overflow: hidden;
  padding-right: 0;
  padding-left: 0;
  z-index: 300;
}

.bz-cd-push-panel.right {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  overflow: hidden;
  padding-right: 0;
  padding-left: 0;
  z-index: 300;
}

.bz-cd-panel-animation.width {
  transition: width 1s ease-in-out;
}

.bz-cd-panel-animation.margin {
  transition: margin 1s ease-in-out;
}

.bz-cd-push-panel {
  position: absolute;
}

.bz-cd-push-panel.right .bz-cd-pr-content,
.bz-cd-panel.right .bz-cd-pr-content {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #FFF;
  border: 1px solid #E2DCCA;
  border-right: none;
}

.bz-cd-push-panel.inside .bz-cd-pr-content {
  background-color: #FFF;
  border: 1px solid #E2DCCA;
  border-right: none;
}

.bz-cd-push-panel.inside {
  overflow: hidden;
  position: absolute;
  right: 0;
}

.invisible {
  visibility: hidden;
  height: 0;
}

[data-sl-content] {
  margin: 0;
  padding: 1em;
}

[data-sl-tmp] {
  margin: 0;
}

.bz-cd-panel.fixed {
  position: fixed;
}

.row-panel-inside {
  padding-top: 1em;
  padding-bottom: 1em;
  position: relative;
}

/**************************
  plugin slide left panel
**************************/

.sl-init .bz-cd-pr-content > div {
  visibility: hidden;
}

.sl-open .bz-cd-pr-content > div {
  visibility: visible;
}

.sl-close {
  width: 0;
  padding-left: 0;
  padding-right: 0;
}

/*******************/

